<template>
  <div v-if="loaded" :class="['app__wrap', { 'transparent-bg': isAiFrame }]">
      <main :class="['app-content', { 'transparent-bg': isAiFrame }]">
        <!-- infobar -->
        <TokenInfoBar v-if="showTokenInfoBar" @open-buy-modal="openBuyModal" />
        <app-bar v-if="showHeader" @toggleAi="toggleAiVisibility"></app-bar>
        <transition name="fade" mode="out-in">
          <router-view class="page-content"></router-view>
        </transition>
        <footer-bar v-if="showFooter"></footer-bar>
        <ai-agent-button v-if="shouldShowAiAgent && showGlobalAiAgent" />
      </main>
    
    <!-- Domain Migration Popup -->
    <domain-migration-popup 
      v-if="showDomainMigrationPopup && isOldDomain" 
      @close="closeDomainMigrationPopup"
    />
    
    <!-- Migration Banner -->
    <migration-banner v-if="isOldDomain" />
    
    <!-- Buy Token Modal -->
    <BuyTokenModal 
      :show="showBuyModal" 
      @close="closeBuyModal"
    />
  </div>
</template>

<script setup>
  import {onMounted, ref, defineExpose, defineAsyncComponent, computed, watch} from 'vue';
  import { useRoute, useRouter } from 'vue-router';
  import store from './vuex';
  import 'solana-wallets-vue/styles.css'
  
  // Заменяем прямые импорты на ленивую загрузку для тяжелых компонентов
  const AppBar = defineAsyncComponent(() => import('./components/AppBar'));
  const FooterBar = defineAsyncComponent(() => import('./components/FooterBar'));
  const AiAgentButton = defineAsyncComponent(() => import('./components/ai/AiAgentButton'));
  const DomainMigrationPopup = defineAsyncComponent(() => import('./components/DomainMigrationPopup'));
  const MigrationBanner = defineAsyncComponent(() => import('./components/MigrationBanner'));
  const TokenInfoBar = defineAsyncComponent(() => import('./components/TokenInfoBar'));
  const BuyTokenModal = defineAsyncComponent(() => import('./components/BuyTokenModal'));
  
  import {
    // LedgerWalletAdapter,
    PhantomWalletAdapter,
    // SlopeWalletAdapter,
    // TorusWalletAdapter,
  } from '@solana/wallet-adapter-wallets';
  import { initWallet } from 'solana-wallets-vue';

  const route = useRoute()
  const router = useRouter()

  const wallets = [
    new PhantomWalletAdapter(),
    // new SlopeWalletAdapter(),
    // new TorusWalletAdapter(),
    // new LedgerWalletAdapter(),
  ];

  initWallet({ wallets, autoConnect: store.state.autoConnect});

  const loaded = ref(false);
  const showAiAgent = ref(true) // Default to true
  const pageScrollPosition = ref(0);
  const showDomainMigrationPopup = ref(false);
  const showBuyModal = ref(false);
  
  // Проверяем текущий домен
  const isOldDomain = computed(() => {
    return window.location.hostname === 'solanabitcat.com'
  });
  
  // Computed property to check if AI agent should be shown
  const shouldShowAiAgent = computed(() => {
    return showAiAgent.value && route.path !== '/ai'
  })

  const showFooter = computed(() => route.path !== '/ai-frame')
  const showGlobalAiAgent = computed(() => route.path !== '/ai-frame')
  const showHeader = computed(() => route.path !== '/ai-frame')
  const showTokenInfoBar = computed(() => route.path !== '/ai-frame')
  const isAiFrame = computed(() => route.path === '/ai-frame')
  
  onMounted(() => {
    console.log('App mounted', route.path)
    // Задержка для обработки реферальных параметров (после инициализации Vue Router)
    setTimeout(() => {
      const urlParams = new URLSearchParams(window.location.search);
      const ref = urlParams.get('r');
      
      if (ref) {
        // Сохраняем реферальный код
        if (!store.getters.isAuthenticated) {
          localStorage.setItem('ref', ref);
        }
        
        // Удаляем параметр и очищаем URL
        urlParams.delete('r');
        const newSearch = urlParams.toString() ? `?${urlParams.toString()}` : '';
        const cleanUrl = `${window.location.pathname}${newSearch}${window.location.hash}`;
        window.history.replaceState(null, '', cleanUrl);
      }
    }, 100);
    loaded.value = true;
    console.log('App mounted');
    // console.log(window.location.hash.startsWith('#/'));
    // console.log(window.location.hash)

    // check path and if /#/path redirect to /path
    // if (window.location.hash.startsWith('#/')) {
    //   router.push(window.location.hash.replace('#/', '/'));
    // }

    const aiEnabled = localStorage.getItem('bitcat_ai_enabled')
    showAiAgent.value = aiEnabled === null ? true : aiEnabled === 'true'
    window.addEventListener('scroll', setPageScrollPosition);
    window.addEventListener("beforeunload", handleBeforeUnload);
    
    // Показываем popup о переходе на новый домен через 10 секунд
    // Но только если он не был показан в этой сессии и мы на старом домене
    if (isOldDomain.value) {
      const domainPopupShown = sessionStorage.getItem('domain_migration_popup_shown')
      if (!domainPopupShown) {
        setTimeout(() => {
          showDomainMigrationPopup.value = true
          sessionStorage.setItem('domain_migration_popup_shown', 'true')
        }, 10000) // 10 секунд
      }
    }
  })

  function handleBeforeUnload() {
    if(window.solana) {
      window.solana.disconnect()
    }
  }

  function setPageScrollPosition() {
    pageScrollPosition.value = window.scrollY;
  }

  const toggleAiVisibility = (value) => {
    showAiAgent.value = value
    localStorage.setItem('bitcat_ai_enabled', value.toString())
  }

  const closeDomainMigrationPopup = () => {
    showDomainMigrationPopup.value = false
  }

  const openBuyModal = () => {
    showBuyModal.value = true;
  };

  const closeBuyModal = () => {
    showBuyModal.value = false;
  };

  defineExpose({
    toggleAiVisibility
  })

  watch(isAiFrame, (val) => {
    console.log('isAiFrame', val)
    const appEl = document.getElementById('app')
    if (appEl) {
      if (val) {
        appEl.style.background = 'transparent'
      }
    }
  }, { immediate: true })

  console.log('Welcome to BITCAT Universe dApp! Enjoy =)');
</script>

