import 'solana-wallets-vue/styles.css'
import './main.css'
import './assets/dark-theme.css'

import { createApp } from 'vue'
import { createRouter, createWebHashHistory, createWebHistory } from 'vue-router'
import { sync } from 'vuex-router-sync'

import routes, { scrollBehavior } from './routes'
import store from './vuex'
import App from './App.vue'

const router = createRouter({
    history: createWebHistory(),
    // history: createWebHashHistory(),
    routes,
    scrollBehavior,
    // Enable automatic prefetch for all routes immediately
    linkPrefetchStrategy: 'all', // Prefetch all links immediately
    prefetchChunkSize: 10000, // Maximum chunk size to prefetch
    prefetchMaxAge: 5000, // Cache prefetched chunks for 5 seconds
    prefetchOnMouseOver: false, // Don't wait for mouseover
    prefetchOnFocus: false // Don't wait for focus
})

sync(store, router);

const app = createApp(App)

app.use(store)
app.use(router)

// Инициализируем авторизацию
store.dispatch('initializeAuth')

// Простая инициализация темы и шрифта
const savedTheme = localStorage.getItem('theme');
if (savedTheme === 'dark') {
  document.documentElement.setAttribute('data-theme', 'dark');
}

const savedFont = localStorage.getItem('font');
if (savedFont === 'serious') {
  document.body.classList.add('font-serious');
}

app.mount('#app')

// Обратная совместимость: перенаправление с хэш-маршрутов на обычные
const redirectHashToPath = () => {
    if (window.location.hash && window.location.hash.startsWith('#/')) {
        const hashPath = window.location.hash.substring(1);
        window.location.href = hashPath;
    }
};

// Проверяем при загрузке страницы после монтирования
router.isReady().then(() => {
    redirectHashToPath();
});

// Обрабатываем изменения хэша
window.addEventListener('hashchange', redirectHashToPath);
